.contacts {
  width: 600px;
  margin: 0 auto 0 0;
  padding: 56px 0 47px 124px;
}

@include vp-1199 {
  .contacts {
    width: 768px;
    margin: 0 auto;
    padding: 63px 0 64px 133px;
  }
}

@include vp-767 {
  .contacts {
    width: 320px;
    padding: 64px 15px 45px;
  }
}

.contacts > span {
  display: block;
  margin: 0 0 39px -3px;
  padding: 0;

  @include ff-alumniSans;
  font-size: 54px;
  font-weight: 600;
  line-height: 65px;
  letter-spacing: 1.62px;
}

@include vp-1199 {
  .contacts > span {
    margin: 0 0 32px 0;
    font-size: 42px;
    line-height: 50px;
    letter-spacing: 1.26px;
  }
}

@include vp-767 {
  .contacts > span {
    margin: 0 0 34px 0;
    font-size: 32px;
    line-height: 38px;
    letter-spacing: 0.96px;
  }
}

.contacts__inner {
  display: grid;
  grid-template-columns: 230px 230px;
  position: relative;
  width: 460px;
  word-break: break-word;
}

@include vp-1199 {
  .contacts__inner {
    width: 475px;
    grid-template-columns: auto auto;
  }
}

@include vp-767 {
  .contacts__inner {
    grid-template-columns: 1fr;
    width: 100%;
  }
}

.contacts__inner-part {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0;
}

@include vp-767 {
  .contacts__inner-part {
    padding: 0;
  }
}

.contacts__inner-part--first-part {
  padding: 0 60px 0 0;
  border-right: 1px solid rgba(116, 129, 156, 1);
}

@include vp-1199 {
  .contacts__inner-part--first-part {
    padding: 0 30px 0 0;
  }
}

@include vp-767 {
  .contacts__inner-part--first-part {
    margin-bottom: 2px;
    border: none;
  }
}

.contacts__inner-part--second-part {
  padding: 0 0 0 60px;
}

@include vp-1199 {
  .contacts__inner-part--second-part {
    padding: 0 0 0 48px;
  }
}


@include vp-767 {
  .contacts__inner-part--second-part {
    padding: 0;
  }
}

.contacts__inner-part span {
  margin: 0 0 2px;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
}

@include vp-1199 {
  .contacts__inner-part span {
    margin-bottom: 5px;
    font-size: 20px;
    line-height: 30px;
  }
}

@include vp-767 {
  .contacts__inner-part span {
    margin-bottom: 6px;
  }
}

.contacts__inner-part p {
  margin: 0 0 18px;
  padding: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: -0.1px;
}

.contacts__inner-part p:last-child {
  margin-bottom: 0;
}

@include vp-767 {
  .contacts__inner-part p:last-child {
    margin-bottom: 18px;
  }
}

@include vp-1199 {
  .contacts__inner-part p {
    font-size: 18px;
    line-height: 27px;
  }
}

.contacts__inner-part a {
  margin: 0 0 18px;
  padding: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: -0.1px;

  &:hover,
  &:focus-visible {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.contacts__inner-part a:last-child {
  margin-bottom: 0;
}

@include vp-767 {
  .contacts__inner-part a:last-child {
    margin-bottom: 18px;
  }
}

@include vp-1199 {
  .contacts__inner-part a {
    font-size: 18px;
    line-height: 27px;
  }
}
