.games-date {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 17px;
  margin: 0;
  padding: 0;
  color: rgba(255, 255, 255, 1);
}

@include vp-1199 {
  .games-date {
    gap: 10px;
  }
}

.games-date__part {
  margin: 0;
  padding: 0;
}

.games-date__part > span {
  display: block;
  margin: 0 0 7px;
  font-size: 18px;
  font-weight: 400;
  line-height: 21px;
}

@include vp-1199 {
  .games-date__part > span {
    margin-bottom: 2px;
    font-size: 12px;
    line-height: 14px;
  }
}

.games-date__part-footer {
  display: grid;
  grid-template-columns: auto auto;
  margin: 0;
  padding: 5px 0 0;
  border-top: 1px solid rgba(255, 255, 255, 1);
}

@include vp-1199 {
  .games-date__part-footer {
    padding-top: 1px;
  }
}

.games-date__part-footer p {
  margin: 0 5px 0 0;
  grid-row: 1 / 3;
  font-size: 40px;
  font-weight: 700;
  line-height: 40px;
}

@include vp-1199 {
  .games-date__part-footer p {
    font-size: 24px;
    line-height: 24px;
  }
}

.games-date__part-footer span {
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}

@include vp-1199 {
  .games-date__part-footer span {
    font-size: 10px;
    line-height: 12px;
  }
}
